.Splash {
  display: flex;
  flex-direction: column;
  flex: 1;
  position: absolute;

  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
  // width: 100%;
  // background: linear-gradient(to top, #303030 30%, #2b2b2b);
  background-color: #2b2b2b;

  align-items: center;
  justify-content: center;

  .animation-container {
    // background-color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -150px;
    margin-top: -150px;

    // background-color: white;

    height: 300px;
    width: 300px;
  }
}

.fadeOut {
  display: none;
  opacity: 0;

  // transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
  transition: opacity 0.3s;
}
.fadeIn {
  opacity: 1;
  // transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
  transition: opacity 1.5s 1.5s;
}

.splash-hide {
  display: none;
}
