.social-border {
  display: flex;
  border-radius: 100%;
  border: 2px solid white;
  // margin: 16px;
}

.social-border:hover {
  background-color: rgba(255, 255, 255, 0.6);
  transition: 300ms;
  cursor: pointer;
}

.social-border:active {
  transform: scale(0.96);
  transition: 300ms;
}

.social {
  display: flex;
}
.social:hover {
  cursor: pointer;
}
