.Contact {
  display: flex;
  flex-direction: column;
  background-color: #3a3a3a;

  height: 100%;
  // min-height: 100vh;
  // height: 850px;

  .arrows-container {
    margin-top: -1px;

    background-color: #ffaa00;

    .arrows-bottom {
      height: 30px;
      width: 100%;

      // height: 800px;
      // width: 300px;

      background-size: contain;
      background-image: url("../../assets/img/arrow-up.svg");
      background-repeat: repeat-x;
      align-items: baseline;
    }
  }

  .content {
    padding: 10% 10% 0% 10%;

    align-items: flex-start;

    .content-quote {
      font-size: 1.5rem;
      color: white;
      font-family: Roboto;
      font-weight: lighter;
      // background-color: lightblue;

      width: 60%;
      margin-top: 80px;
      text-align: start;

      font-family: Aileron Light;
      letter-spacing: 3px;
    }

    .social-content {
      display: flex;
      // display: none;
      // background-color: lightyellow;
      align-items: center;
      width: 100%;

      margin-top: 60px;
      // align-items: stretch;
      justify-content: space-between;

      .mail-content {
        display: flex;

        flex-direction: column;
        align-items: flex-start;

        h1 {
          color: white;
          padding: 0px 0px 16px 0px;
        }
        .email {
          color: #ffaa00;
          font-weight: bold;
        }

        .email:hover {
          cursor: pointer;
        }
      }
      .social-buttons {
        display: flex;
        flex-wrap: nowrap;
        align-items: center;
        justify-content: flex-end;
        // background-color: lightblue;
        width: 50%;

        .social-container {
          padding: 0px 8px 0px 8px;
        }
        .social-container:hover {
          opacity: 0.3;
          transition: 300ms;
          cursor: pointer;
        }
      }
    }
  }
  .footer {
    // margin-bottom: -10px;
    margin-top: 80px;
  }
}

@media screen and (min-width: 1025px) and (max-width: 1200px) {
  .Contact {
    .content {
      .content-quote {
        width: 100%;
        margin-top: 60px;
      }
      .social-content {
        // margin-top: 310px;
        .social-buttons {
          justify-content: flex-end;
        }
      }
    }
    .footer {
      // align-self: baselin/e;
      // align-self: inherit;
      margin-top: 100px;
    }
  }
}

@media screen and (max-width: 1024px) {
  .Contact {
    height: 100%;
    .content {
      // background-color: lightblue;

      .content-quote {
        width: 100%;
        // margin-top: 60px;
      }
      .social-content {
        flex-direction: column;
        align-items: flex-start;
        // align-items: center;
        margin-top: 60px;

        .social-buttons {
          // display: none;
          margin-top: 48px;
          flex-wrap: nowrap;
          align-items: center;
          justify-content: center;
          width: 100%;
        }
      }
    }

    .footer {
      margin-top: 0px;
    }

    .arrows-container {
      margin-bottom: -2px;

      .arrows-bottom {
        margin-bottom: -1px;
      }
    }
  }
}
