.Navbar {
  display: flex;

  position: fixed;
  padding: 16px 20px 20px 30px;
  z-index: 999;

  width: 100%;

  align-items: center;
  justify-content: left;

  .link {
    margin: 0px 16px 0px 16px;
    padding: 0px 8px 0px 8px;
    text-align: center;

    color: white;
    font-size: 1em;
    letter-spacing: 6px;
    font-family: Aileron Light;
  }

  .link:hover {
    cursor: pointer;
  }

  .active {
    color: white;
    // border-bottom-width: 100%;
    border-bottom: 2px solid orange;
  }
}

.bg-color::before {
  opacity: 0;
  background-color: rgba(58, 58, 58, 0.9);
  transition: 300ms;
}

.bg-color {
  opacity: 1;
  background-color: rgba(58, 58, 58, 0.9);
  transition: 300ms;
}

@media screen and (max-width: 1024px) {
  .Navbar {
    display: none;
  }
}
