.Sponsor {
  background-color: #ffaa00;

  display: flex;

  flex-direction: column;
  height: 90vh;

  .arrow {
    display: flex;
    align-self: center;
    margin-left: 30px;

    width: 0;
    height: 0;
    border-left: 90px solid transparent;
    border-right: 90px solid transparent;

    border-top: 30px solid white;
  }

  img {
    align-self: center;
    padding: 30px;
    margin-bottom: 40px;
    max-width: 400px;
  }

  .sponsor-image {
    min-width: 50%;
    max-width: 80%;
    object-fit: contain;
  }

  .sponsor-image:hover {
    transform: scale(0.95);
    cursor: pointer;
    transition: 250ms;
  }
}

@media screen and (max-width: 1024px) {
  .Sponsor {
    height: 100%;

    .arrow {
      margin-top: -2px;
    }
  }
}
