.Home {
  min-height: 800px;
  height: 100vh;

  padding-right: 0;
  padding-right: env(safe-area-inset-right);
  padding-left: 0;
  padding-left: env(safe-area-inset-left);

  // height: 800px;
  // height: 1800px;
  // width: 100%;
  display: flex;
  flex-direction: row;
  flex: 1;

  background: linear-gradient(to top, #303030 30%, #2b2b2b);
  overflow: hidden;

  .content {
    display: flex;
    flex: 0.6;

    align-items: center;
    justify-content: center;
    flex-direction: column;
    // margin-bottom: 60px;
    padding: 16px;

    .social-container {
      display: flex;
      width: 100%;
      align-items: center;
      justify-content: center;
      flex-direction: row;
      margin: 16px 0px 0px 0px;

      .social-container-margin {
        margin: 16px;
      }
      // background-color: lightblue;
    }

    h1 {
      font-size: 4rem;
      letter-spacing: 16px;
      color: white;

      font-family: Aileron;
    }

    ul {
      // background-color: lightcoral;
      margin-bottom: 16px;
      li {
        padding: 8px 16px 8px 16px;
        display: inline-block;

        font-weight: bold;
        font-family: Aileron;
        color: #ffaa00;
        letter-spacing: 6px;
      }
    }

    .description-text {
      // display: none;
      // background-color: lightblue;
      padding: 16px;
      color: white;
      // margin: 32px 120px 0px 120px;
      text-align: left;

      width: 65%;
      // max-width: 500px;

      font-size: 1.2rem;
      font-family: Aileron Light;
      letter-spacing: 0.4rem;
      line-height: 24pt;
    }
  }

  .img-profile {
    // position: absolute;
    // // bottom: 65px;
    // bottom: 0;
    // right: 0;
    display: flex;
    flex: 0.4;
    height: 100%;
    // background-color: lightblue;
    align-self: flex-end;
    align-items: flex-end;

    // width: 750px;
    // width: 50%;
    max-width: 900px;
    min-width: 600px;

    margin-bottom: -10px;
    // background-color: lightblue;
    // min-width: 600px;
    // margin-right: -80px;

    .image {
      width: 100%;
    }
  }
  .triangle-01 {
    // align-self: flex-end;
    position: absolute;
    height: 10%;
    bottom: 0;
    right: 0;

    background-color: lightblue;

    // width: 100px;
    width: 80%;
    object-fit: cover;
    // margin-bottom: -110px;
    margin-bottom: -1px;
    user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
  }

  .triangle-02 {
    display: none;
    position: absolute;
    bottom: 0;
    left: 0;
    object-fit: cover;
    // width: 30%;

    // transform: rotateY(180deg);
    // margin-bottom: -5px;
    user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    // }
  }
}

//481
@media screen and (min-width: 1025px) and (max-width: 1200px) {
  .Home {
    .content {
      padding: 16px 32px 16px 32px;

      h1 {
        font-size: 2rem;
      }

      ul {
        li {
          padding: 8px;
          letter-spacing: 3px;
        }
      }

      .description-text {
        width: 85%;
        font-size: 1rem;
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  .Home {
    display: flex;
    // height: 900px;
    height: 90vh;
    flex-direction: column;

    .content {
      flex: 1;
      padding: 64px 32px 32px 32px;

      h1 {
        font-size: 1.5rem;
        letter-spacing: 8px;
        padding: 0px 0px 16px 0px;
      }

      ul {
        margin-bottom: 3px;
        display: flex;
        li {
          padding: 4px 8px 4px 8px;
          font-size: 0.69rem;
          letter-spacing: 3px;
        }
      }

      .description-text {
        display: none;
        width: 100%;
        padding: 16px;
      }

      .social-container {
        .social-container-margin {
          margin: 8px;
        }
      }
    }

    .img-profile {
      display: flex;
      align-self: center;
      justify-content: center;

      .image {
        // width: 80%;
        height: 100%;
        object-fit: contain;
      }
    }

    .triangle-02 {
      display: none;
      position: absolute;
      top: 0;
      left: 0;

      background-color: white;
      width: 375px;
      // margin-bottom: -100px;
      height: 300px;
      // align-self: flex-end;
      // background-color: lightblue;
    }
  }
}
