.Text {
  display: flex;
  flex-direction: column;
  // flex: 1;
  // background-color: lightblue;
  // padding: 28px;

  .header-left {
    font-family: Aileron Black;
    text-align: left;
  }

  .header-right {
    font-family: Aileron Black;
    text-align: right;
  }

  .divider {
    display: flex;
    width: 300px;
    height: 3px;
    margin-top: 16px;
    background-color: #ffaa00;
  }
}

@media screen and (max-width: 480px) {
  .Text {
    display: flex;
    width: 100%;
    font-size: 0.8rem;

    .divider {
      width: 80%;
    }
  }
}
