.LatestVideo {
  display: flex;
  flex-direction: column;
  align-self: center;

  background-color: white;

  min-height: 80vh;

  padding: 38px;

  .video-content {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 16px;
    margin-top: 8px;

    .channel-title {
      transform: rotate(180deg);
      writing-mode: vertical-rl;
      font-family: Aileron Light;
      letter-spacing: 8px;
      text-align: center;

      font-size: 1.5rem;
    }

    .divider {
      width: 1px;
      margin: 20px 0 20px 0;
      background-color: black;
      opacity: 0.3;
      margin: 8px;
    }

    .video-container {
      display: flex;
    }
  }
}

@media screen and (max-width: 1024px) {
  .LatestVideo {
    height: auto;
    min-height: 50vh;

    justify-content: center;
  }
}
