.Video {
  display: flex;
  width: auto;
  max-width: 500px;
  margin: 18px;
  // background-color: orange;
  flex-direction: column;

  .thumbnail {
    box-shadow: 5px 3px 10px #888888;

    border-radius: 3px;
  }

  .description {
    display: flex;
    align-items: center;
    padding-top: 8px;

    .content {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-left: 8px;

      h1 {
        font-family: Aileron;
        font-size: 1rem;
        text-align: start;
        letter-spacing: 2px;
      }
      p {
        text-align: start;
        // font-size: 12px;
        margin-top: 3px;
        font-family: Aileron Light;
      }
    }
  }
}

.Video:hover {
  transform: scale(0.95);
  transition: 300ms;
  cursor: pointer;
}

.Video:active {
  transform: scale(0.93);
  transition: 300ms;
}
